// ANIMACIONES

.title-animation {
  font-size:36px;
  color:#ecf0f1;
  // background-color: purple;
	display: flex;
	overflow: hidden;
	text-align: center;
	width: auto;
	margin: 0 auto;
  &:before {
    content:'[';
    line-height:47px;
  }
  &:after {
    content:']';
    line-height:47px;
  }
  &:after, &:before {
    color:#16a085;
    font-size:42px;
    animation:2s linear 0s normal none infinite opacity;
    -webkit-animation:2s ease-out 0s normal none infinite opacity;
    -moz-animation:2s ease-out 0s normal none infinite opacity;
    -o-animation:2s ease-out 0s normal none infinite opacity;
  }
}


.title-animation-visible {
		float:left;
	  font-weight:600;
	  overflow:hidden;
		margin-left: 10px;
		z-index: 100;
}


.title-animation-ul {
  margin-top:0;
  padding-left:110px;
	margin-left: -99px;
	margin-right: 10px;
  text-align:left;
  list-style:none;
  animation:6s linear 0s normal none infinite change;
  -webkit-animation:6s linear 0s normal none infinite change;
  -moz-animation:6s linear 0s normal none infinite change;
  -o-animation:6s linear 0s normal none infinite change;
		// background-color: red;
		overflow: hidden;
		font-size: 36px;
		color: $color-primary;
		font-weight: bold;
	}

.title-animation-ul li {
  margin:0;
	line-height:40px;
	height: 40px;
	// background-color: turquoise;
}

@-webkit-keyframes opacity {
  0%   {opacity:0;}
  50%  {opacity:1;}
  100% {opacity:0;}
}
@keyframes opacity {
  0%   {opacity:0;}
  50%  {opacity:1;}
  100% {opacity:0;}
}

@-webkit-keyframes change {
  0%   {margin-top:0;}
  15%  {margin-top:0;}
  25%  {margin-top:-40px;}
  40%  {margin-top:-40px;}
  50%  {margin-top:-80px;}
  65%  {margin-top:-80px;}
  75%  {margin-top:-40px;}
  85%  {margin-top:-40px;}
  100% {margin-top:0;}
}
@keyframes change {
  0%   {margin-top:0;}
  15%  {margin-top:0;}
  25%  {margin-top:-40px;}
  40%  {margin-top:-40px;}
  50%  {margin-top:-80px;}
  65%  {margin-top:-80px;}
  75%  {margin-top:-40px;}
  85%  {margin-top:-40px;}
  100% {margin-top:0;}
}

